import React, { useState } from 'react';
import QrReader from 'react-qr-reader';

const HomePage = () => {
  const [qrData, setQrData] = useState('');

  const handleScan = (data) => {
    if(data) {
      setQrData(data);
    }
}

  const handleError = (err) => {
    console.error(err)
  }

  // const qrReaderClick = useRef(null);
  
  // const allowHandler = () => {
  //   qrReaderClick.current.openImageDialog();
  // }

  return (
    <div>
      <div className="Home__page">
        <QrReader
          // ref={qrReaderClick}
          delay={300}
          onError={handleError}
          onScan={handleScan}
          className="qrReader"
        />
      </div>
      result of qr scanning is  {qrData}
    </div>
  );
};

export default HomePage;
