import React, { useState } from 'react';
import BarcodeScannerComponent from "react-webcam-barcode-scanner";

const BarCode = () => {

  const [barCode, setBarCode] = useState('Nothing');

  const handleScan = (er, data) => {
    if (data) {
      setBarCode(data.text);
    } else {
      setBarCode('Not Found')
    }

  }

  return (
    <div>
      <BarcodeScannerComponent 
          onUpdate={handleScan}
          className="barcodeReader"
      />
        <p>{barCode}</p>
    </div>
  );
}

export default BarCode;
